@media only screen and (max-width: 767px) { .popup .mfp-close { top: 16px; right: 16px; } }

.popup .mfp-close:hover { box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1); }

.mfp-container { padding: 40px 30px 20px; }

@media only screen and (max-width: 1199px) { .mfp-container { padding: 40px; } }

@media only screen and (max-width: 767px) { .mfp-container { padding: 0; } }

@media (max-width: 900px) { .mfp-container { padding: 40px; } }

.mfp-bg { background: url("../img/bg-popup.png") no-repeat 50% 20%/cover; }

.mfp-zoom-in .popup { opacity: 0; transition: all 0.2s ease-in-out; transform: scale(0.8); }

.mfp-zoom-in.mfp-bg { opacity: 0; transition: all 0.2s ease-out; }

.mfp-zoom-in.mfp-ready .popup { opacity: 1; transform: scale(1); }

.mfp-zoom-in.mfp-ready.mfp-bg { opacity: 1; }

.mfp-zoom-in.mfp-removing .popup { transform: scale(0.8); opacity: 0; }

.mfp-zoom-in.mfp-removing.mfp-bg { opacity: 0; }

[data-aos="animation-scale-x-left"] { transform: translateX(-50px) scaleX(1.1); opacity: 0; transition-property: transform, opacity; }

[data-aos="animation-scale-x-left"].aos-animate { transform: translateX(0) scaleX(1); opacity: 1; }

[data-aos="animation-scale-x-right"] { transform: translateX(50px) scaleX(1.1); opacity: 0; transition-property: transform, opacity; }

[data-aos="animation-scale-x-right"].aos-animate { transform: translateX(0) scaleX(1); opacity: 1; }

[data-aos="animation-scale-y"] { transform: translateY(40px) scaleY(1.4); opacity: 0; transition-property: transform, opacity; }

[data-aos="animation-scale-y"].aos-animate { transform: translateY(0) scaleY(1); opacity: 1; }

[data-aos="animation-scale-x"] { transform: translateX(-40px) scaleX(1.1); opacity: 0; transition-property: transform, opacity; }

[data-aos="animation-scale-x"].aos-animate { transform: translateX(0) scaleX(1); opacity: 1; }

[data-aos="animation-translate-y"] { transform: translateY(40px); opacity: 0; transition-property: transform, opacity; }

[data-aos="animation-translate-y"].aos-animate { transform: translateY(0); opacity: 1; }
